'use client';

import React from 'react';
import { useTranslation } from 'next-i18next';
import Link from 'next/link';
import styles from './Navbar.module.css';

function Navbar(props) {
    const {t: translate, i18n} = useTranslation('navbar');

    function handleChangeLanguage(event){
        const selectedLanguage = event.target.value
        i18n.changeLanguage(selectedLanguage);
    }

    return (
        <nav className={styles.navbar}>
            <div className={styles.logo}>
            <Link href='/'>MR</Link>
            </div>
            <div className={styles.btnBox}>
                <Link href='/'>{translate('navbar.mainPage')}</Link>
                <Link href='/about-me'>{translate("navbar.aboutMe")}</Link>
                <Link href='/projects'>{translate("navbar.projects")}</Link>
                <Link href='/contact'>{translate("navbar.contact")}</Link>
                <select name='language' defaultValue={i18n.language} onChange={handleChangeLanguage}>
                    <option value={'en'}>EN</option>
                    <option value={'pl'}>PL</option>
                </select>
            </div>
        </nav>
    );
}

export default Navbar;